

/* eslint-disable no-unused-vars */


import React, { Component } from "react"
import MetaTags from 'react-meta-tags';
import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  CardHeader,
  CardTitle,
  FormGroup,
  Label,
  Button,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Breadcrumb,
  BreadcrumbItem,
} from "reactstrap"

import {
  CButton,
  CCard,
  CCardHeader,
  CCardBody,
  CCardFooter,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react'
import Select from "react-select"
import axios from 'axios'
import  { Redirect } from 'react-router-dom'

import { French } from "flatpickr/dist/l10n/fr.js";

// import Flatpickr from "react-flatpickr"
import Flatpickr from "flatpickr";

import DatePicker from "react-datepicker";
import { registerLocale, setDefaultLocale } from  "react-datepicker";
import es from 'date-fns/locale/es';
import $ from 'jquery';
import conf from '../../../../conf.js';
import ReactSelect from 'react-select'
import makeAnimated from 'react-select/animated';
// import ville from "../../dict/ville.js";
import ModalMessage from "../../../pages/messages/ModalMessage"
import { storage } from "../../../../firebase/firebase";

const animatedComponents = makeAnimated();


// const cities = require('all-the-cities');

registerLocale('es', es)

class AjoutHomeOne extends Component {
    constructor(props) {
        super(props)
        this.state = {
          ok:"",
          files_preview:"",
          files_name:{},
          multimedia:{file:""},
          show:false,
          success:true,
          boutton:"OK",
          txt_button:"Autre",
          message:"homeone ajout avec succs !!",
          titre:"Message ajout de homeone"
        }
        this.handleChangeButton.bind(this)
    }

  componentDidMount() {
    $("input[data-preview]").change(() =>{
      var data=[]
      console.log("fiel =====>",Array.from(document.getElementById("autreFile").files))
      var date=new Date()
      
      var my_files = Array.from(document.getElementById("autreFile").files)
        var fileNom=date.getFullYear()+""+date.getMonth()+""+date.getDay()+""+date.getSeconds()+""+date.getMilliseconds()+""+my_files[0].name
        this.setState({files_name:{file:my_files[0],fileName:fileNom},multimedia:{file:fileNom}})
        
        var fileReader = new FileReader();
        fileReader.readAsDataURL(my_files[0]);
        fileReader.onload = (fileEvent)=> {
          this.setState({files_preview:fileEvent.target.result})
    
        };

    
    });
  }

  handleAddHome=()=>{
    var media=null
    if(this.state.files_name.file!=""){
       media=this.state.multimedia
      storage.ref("images/"+this.state.files_name.fileName).put(this.state.files_name.file).then(()=>{})
     
    }
    
    
    let  token=  JSON.parse(localStorage.getItem('token_auth'))
    var data= {
      "description": $("#description").val(),
      "titre":$("#titre").val(),
      "bouton":this.state.txt_button,
      "multimedia":media
    }

    console.log("data a transmettre",data)

    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json','Accept': 'application/json',
      'Authorization':'Bearer '+token.token,
    },
      body: JSON.stringify(data)
  };

  fetch(conf.url+"homeone/add",requestOptions)
  .then((response) => response.json())
  .then((data) => {
     console.log("okkk====>",data)
    

     try{
      if(data.id){
       this.setState({show:true,success:true});
      }else{
       this.setState({show:true,success:false});
      }
   }catch(err){
     this.setState({show:true,success:false});
   }
  
   }
   
  );

  }

  handleRetour=()=>{
    this.setState({ok: <Redirect to='/dynamic-form-accueil'/>});
  }

  handleChangeButton=(e)=>{
    console.log("adate =====>",e)
    try{
      this.setState({txt_button:e.value})
     
    }catch(ee){

    }
  
  }
  handleValidate=()=>{
    if(this.state.success){
      this.setState({ok: <Redirect to='/dynamic-form-accueil'/>});
    }else{
      this.setState({show:false})
    }
    
  }
   
  render() {

  const { pick } = this.state
  const { selectedGroup } = this.state
  const { selectedLangue } = this.state
  const animatedComponents = makeAnimated();

  const optionType = [
    {
      options: [
        { label: "Participer", value:"Participer" },
        { label: "Rserver", value:"Rserver" },
        { label: "Autre", value:"Autre" },
      ]
    },
  ]
  return (
        <>
          <React.Fragment>
            <div className="page-content">
             {this.state.ok}

             
             <ModalMessage
              show={this.state.show}
              success={this.state.success}
              boutton={this.state.boutton}
              handleValidate={this.handleValidate}
              message={this.state.message}
              titre={this.state.titre}
              onCloseClick={() => this.setState({ show: false })}
              />

            <Container fluid={false} className="justify-content-center">

              <Row className="justify-content-center">
              <Col md="12" lg="12" xl="12">

                {/* <Row ClassName="mb-3">
                  <Col md="8">
                      <div className="page-title-box d-flex align-items-center justify-content-between">
                        <h4 className="mb-0 font-size-18">Nouveau compte</h4>
                      </div>
                  </Col>
                </Row> */}
     
                

                <Card ClassName="mt-5">
                  {/* <CCardHeader className="p-4"> */}
                    <h1 className="p-4" >Ajouter un HomeOne</h1>
                  {/* </CCardHeader> */}


                  <CardBody>
                    
                  <Row > 
                      <Col md="12">
                        <label>Titre</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="titre"
                          placeholder="Titre"
                        />
                      </Col>
                    </Row>

                    <Row className="mt-3">
                      <Col md="12">
                        <label>Description</label>
                        <textarea  className="form-control" id="description" placeholder="Description" />
                      </Col>
                    </Row>


                    <Row > 
                      <Col md="6" className="mt-3">
                        <label>Image</label>
                        
                        <Input
                          className="form-control"
                          type="file" 
                          data-preview=".preview"
                          
                          id="autreFile"
                        />
                      </Col>
                      <Col md="6" className="mt-3">
                        <label>Boutton</label>
                        <ReactSelect
                          closeMenuOnSelect={true}
                          components={animatedComponents}
                          onChange={this.handleChangeButton}
                           options={optionType}
                          placeholder="Boutton"
                        />
                      </Col>
                    </Row>

                    <Row className="mt-2">
                             <Col md="12">
                                    <Card>
                                       
                                        <CardBody>
                                            <img style={{width:"900px",heigth:"200px"}} className="card-img-top" src={this.state.files_preview} alt="Image"/>
                                        </CardBody>
                                        
                                       
                                    </Card>
                              </Col>
                      </Row>


               
                    <Row className="justify-content-start mt-5">
                      <Col md="6" className="mt-1">
                        <Button id="initial" onClick={this.handleAddHome} color="success" block className="b-lg" >
                          Ajouter le Home One
                        </Button>
                        {/* <Button id="chargement" color="info" className="b-lg" type="button" disabled>
                        <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"> </span>
                          Envoi...
                        </Button> */}
                      </Col>

                      <Col md="6" className="mt-1">
                      <Button  onClick={this.handleRetour} color="secondary" block className="b-lg">
                          Annuler
                        </Button>
                      </Col>
                      
                    </Row>


                  </CardBody>
                </Card>
              </Col>
            </Row>
          </Container>
        </div>
             
      </React.Fragment>
    </>
        )}

  }
  export default AjoutHomeOne